.title{
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  font-size: 50px;
  line-height: 58.59px;
  margin-bottom: 89px;
}

.content{
  display: flex;
  margin-bottom: 60px;
}

@media (max-width: 1030px){
  .title{
    text-align: center;
    margin-top: 60px;
    margin-bottom: 40px;
  }
  .content{
    flex-direction: column;
    align-items: center;
  }
}




