.title{
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  font-size: 50px;
  line-height: 58.59px;
  margin-bottom: 70px;
}

.content{
  display: flex;
  justify-content: space-around;
  margin-bottom: 40px;
}

.img{
  border-radius: 50%;
  width: 350px;
  height: 350px;
  margin-right: 100px;
}

.text{
  font-family: 'Montseratt', sans-serif;
  text-align: center;
}

@media (max-width: 1030px){
  .title{
    text-align: center;
    margin-top: 60px;
    margin-bottom: 40px;
  }
  .content{
    flex-direction: column;
    align-items: center;
  }
  .img{
    margin-right: 0;
    margin-bottom: 30px;
  }
}

