.item{
  width: calc(100%/4);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img{
  height: 154px;
  margin-bottom: 45px; 
}

.text{
  font-family: 'PT Serif';
  text-align: center;
}

@media (max-width: 750px) {
  .item{
    width: calc(100%/2);
    margin-bottom: 40px;
  }
}

@media (max-width: 550px){
  .item{
    width: 100%;
  }
}