.title{
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  font-size: 50px;
  line-height: 58.59px;
  margin-bottom: 89px;
}

.content{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 62px;
}

@media (max-width: 1030px){
  .title{
    text-align: center;
    margin-top: 60px;
    margin-bottom: 30px;
  }
}

@media (max-width: 550px){
  .content{
    margin-bottom: 20px;
  }
}
